  background: rgba(0, 0, 0, 0) none repeat scroll 0 0;
  border: medium none;
  color: #4c4c4c;
  font-size: 15px;
  min-height: 48px;
  width: 100%;
}

.input_msg_write input:focus {
  outline: none;
}

.type_msg {
  border-top: 1px solid #c4c4c4;
  position: relative;
}
.msg_send_btn {
  background: #05728f none repeat scroll 0 0;
  border: medium none;
  border-radius: 50%;
  color: #fff;
  cursor: pointer;
  font-size: 17px;
  height: 33px;
  position: absolute;
  right: 0;
  top: 11px;
  width: 33px;
}
.messaging {
  padding: 0 0 50px 0;
}
.msg_history {
  height: 516px;
  overflow-y: auto;
}

@media (max-width: 768px) {
  .srch_bar {
    display: none;
  }

  .chat_ib .chat_date {
    display: none;
  }

  .chat_ib p {
    display: none;
  }

  .chat_ib h5 {
    font-size: 10px;
    margin-left: -0.5rem;
    margin-top: 0.5rem;
  }

  .inbox_people {
    width: 20%;
  }

  .mesgs {
    float: left;
    padding-left: 20px;
    padding-top: 20px;
    padding-right: 0px;
    width: 80%;
  }

  .chat_img {
    width: 100%;
  }
}

